<template>
  <v-data-table
  :headers="headers"
  :items="minums"
  sort-by="calories"
  class="elevation-1"
  >
  <template v-slot:top>
  <v-toolbar flat color="white">
  <v-toolbar-title>Crud Atma pedia</v-toolbar-title>
  <v-divider
  class="mx-4"
  inset
  vertical
  ></v-divider>
  <v-spacer></v-spacer>
  <v-dialog v-model="dialog" max-width="500px">
  
  <template v-slot:activator="{ on }">
  <v-btn color="primary" dark class="mb-2" v-on="on">buat minuman</v-btn>
  <v-btn color="error" dark class="mb-2" @click="deleteMultiple(item)">DELETE{{selected.length}}</v-btn>
  </template>
  <v-card>
  <v-card-title>
  <span class="headline">{{ formTitle }}</span>
  </v-card-title>
  <v-card-text>
  <v-container>
  <v-row>
  <v-col cols="12" sm="6" md="4">
    <v-text-field v-model="editedItem.name" label="Drink name"></v-text-field>
  </v-col>
  <v-col cols="12" sm="6" md="4">
    <v-select  v-model="editedItem.calories" :items="items" label="Type"></v-select>
  </v-col>
  <v-col cols="12" sm="6" md="4">
  <v-text-field v-model="editedItem.fat" label="Fat (g)"></v-text-field>
  </v-col>
  <v-col cols="12" sm="6" md="4">
  <v-text-field v-model="editedItem.carbs" label="Carbs (g)"></v-text-field>
  </v-col>
  <v-col cols="12" sm="6" md="4">
  <v-text-field v-model="editedItem.protein" label="Protein (g)"></v-text-field>
  </v-col>
  </v-row>
  </v-container>
  </v-card-text>
  <v-card-actions>
  <v-spacer></v-spacer>
  <v-btn color="blue darken-1" text @click="close">Cancel</v-btn>
  <v-btn color="blue darken-1" text @click="save">Save</v-btn>
  </v-card-actions>
  </v-card>
  </v-dialog>
  </v-toolbar>
  </template>
  <template v-slot:item.checkbox="{ item }">
    <v-checkbox v-model="selected" v-bind:value="item['.key']"></v-checkbox>
  </template>
  <template v-slot:item.action="{ item }">
    <v-icon class="mr-1" @click="editItem(item)">mdi-silverware</v-icon>
    <v-icon class="mr-1" @click="deleteItem(item)">mdi-delete</v-icon>
  </template>
  </v-data-table>
  </template>
  <script>
  import { drinkRef } from '../firebase';
  export default {
  data: () => ({
  dialog: false,
  selected:[],
  cek: -1,
  items: ['Es', 'Hangat'],
  headers: [
  {
  text: 'Drinks (100g serving)',
  align: 'left',
  sortable: false,
  value: 'name',
  },
  { text: 'Calories', value: 'calories' },
  { text: 'Fat (g)', value: 'fat' },
  { text: 'Carbs (g)', value: 'carbs' },
  { text: 'Protein (g)', value: 'protein' },
  { text: 'Cek', value: 'checkbox', sortable: false },
  { text: 'Actions', value: 'action', sortable: false },
  ],
  minums: [],
  editedIndex: -1,
  editedItem: {
  name: '',
  calories: 'Es',
  fat: 0,
  carbs: 0,
  protein: 0,
  },
  defaultItem: {
  name: '',
  calories: 0,
  fat: 0,
  carbs: 0,
  protein: 0,
  },
  }),
  firebase: {
    minums : drinkRef
  },
  computed: {
    formTitle () {
      return this.cek === -1 ? 'New Item' : 'Edit Item'
    },
  },
  watch: {
  dialog (val) {
  val || this.close()
  },
  },
  methods: {
  close () {
  this.dialog = false
  },
  save() {
    if(this.cek>-1) {
      drinkRef.child(this.editedIndex).set({name: this.editedItem.name, calories: this.editedItem.calories,
    fat: this.editedItem.fat, carbs:this.editedItem.carbs, protein: this.editedItem.protein})
    this.clear()
    } else {
      drinkRef.push({name: this.editedItem.name, calories: this.editedItem.calories, 
    fat: this.editedItem.fat, carbs: this.editedItem.carbs, protein: this.editedItem.protein})
    this.clear()
    }
  this.close()
  },
  clear() {
  this.editedItem={}
  this.editedItem.name= ''
  this.editedItem.calories= 'Es'
  this.editedItem.fat= 0
  this.editedItem.carbs= 0
  this.editedItem.protein= 0
  this.cek=-1
  },
   editItem (item) {
    this.editedIndex = item['.key']
    this.cek = 0
    this.editedItem = Object.assign({}, item)
    this.dialog = true
  },
  deleteItem (item) {
    confirm('Are you sure you want to delete this item?') && drinkRef.child((item['.key'])).remove()
    this.selected=[]
  },
  deleteMultiple(){
    for(var i=0;i<this.selected.length;i++)
    {
      drinkRef.child((this.selected[i])).remove()
    }
    this.selected=[]
  },
  },
  }
  </script>